
import { Component } from "react";
import side_two from "./resources/side_two.zip"


class Sect extends Component {
  constructor( props ) {
    super();
    this.name = props.name | "default";
    this.state = {active: false};
  }

  setActive(active) {
    this.setState( {active: active } );
  }

  renderContent() {
    return (
      <div>
        default content
      </div>
    );
  }

  render() {
    return (
      <div className={ this.state.active ? "active" : "" }>
        <div className={"sect-content"}>
          {this.renderContent()}
        </div>
      </div>
    );
  }
}


class PortfolioSect extends Sect {
  constructor(props) {
    super(props);
    this.state.active = true;
  }

  renderContent() {
    return (
    <div>
      <h1>
        Portfolio
      </h1>
      <ul>

        <li>
          <h2>Momument Mythos</h2>
          <p>
            My work with Alex Casanas on his ARG series, the <em>Monument Mythos</em>.
          </p>
            <iframe style="border: 0; width: 100%; height: 120px;" src="https://bandcamp.com/EmbeddedPlayer/album=982479502/size=large/bgcol=333333/linkcol=ffffff/tracklist=false/artwork=small/transparent=true/" seamless>
              <a href="https://electricsheap.bandcamp.com/album/music-of-the-mythos-volume-one">Music of the Mythos - Volume One by AJCW</a>
            </iframe>
          <p>
            Below is a link to side two of the album. 
            <a href={side_two}>side_two.zip</a>
          </p>
        </li>
      </ul>
    </div>);
  }
}
  
export { Sect, PortfolioSect };